// MAIN COLORS
$white-color: #fff;
$offwhite-color: rgba(255, 255, 255, 0.7);
$black-color: #1e1f22;
$grey-color: grey;


// GRADIENTS
// black-black
$grey-dark-gradient: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
// grey-white
$grey-light-gradient: linear-gradient(90deg, rgba(227, 227, 220, 0.9) 0%, rgba(255, 253, 255, 1) 100%);
// white-gradient
$white-gradient: linear-gradient(90deg, rgb(214, 212, 212) 50%, rgb(229, 227, 227) 100%);
// grey-black
$grey-black-gradient: linear-gradient(145deg, rgba(123, 123, 123, 1) 0%, rgba(0, 0, 0, 1) 100%);
// black-grey
$black-grey-gradient: linear-gradient(145deg, rgba(0, 0, 0, 1) 0%, rgba(123, 123, 123, 1) 100%);
// orange-salmon
$orange-salmon-gradient: linear-gradient(-45deg, #ee7752, #e73c7e);
// blue
$blue-gradient: linear-gradient(152deg, rgba(0, 255, 248, 1) 0%, rgba(58, 114, 180, 1) 77%);
// blue reverse
$blue-reverse-gradient: linear-gradient(152deg, rgba(58, 114, 180, 1) 23%, rgba(0, 255, 248, 1) 100%);
// red
$red-gradient: linear-gradient(138deg, rgba(255, 0, 0, 0.96) 0%, rgba(255, 0, 0, 1) 100%);
// red reverse
$red-reverse-gradient: linear-gradient(138deg, rgba(255, 0, 0, 1) 0%, rgba(255, 0, 0, 0.96) 100%);;
// green
$green-gradient: linear-gradient(138deg, rgba(109, 226, 103, 1) 0%, rgb(68, 191, 19) 100%);
// green reverse
$green-reverse-gradient: linear-gradient(138deg, rgba(68, 191, 19) 0%, rgba(109, 226, 103, 1) 100%);

// yellow-orange
$yellow-orange-gradient: linear-gradient(90deg, rgba(255, 255, 0, 1) 0%, rgba(233, 30, 99, 1) 50%, rgba(255, 59, 0, 1) 100%);

// black
$black-color-gradient: linear-gradient(90deg, rgba(30, 31, 34, 1) 0%, rgba(30, 31, 34, 1) 50%, rgba(30, 31, 34, 0.1) 80%, rgba(30, 31, 34, 0) 100%);


// TRANSITIONS
$transit-quick: all .3s ease-in-out;